.enrol-button {
    display: block;
    margin: 0 auto;
    width: 280px;
    max-width: 400px;
    background-color: $button-color;
    text-decoration: none;
    text-transform: uppercase;
    text-align: center;
    padding: 20px 10px;
    box-sizing: border-box;
    @include buttons;
}